import { mapState } from "vuex";
//引入swiper的js
import Swiper from "swiper";
export default {
  name: "ListContainer",
  mounted() {
    //通知vuex把轮播图数据存到仓库中
    this.$store.dispatch("reqGetBannerList");

    //下面这种写法:存在问题,定时器事件,没有办法去预估,刷新页面时小圆点不会出现，更好的解决方法是使用watch+$nextTick
