    // setTimeout(() => {
    //   //初始化Swiper类的实例
    //   let mySwiper = new Swiper("#mySwiper", {
    //     //设置轮播图防线
    //     direction: "horizontal",
    //     //开启循环模式
    //     loop: true,
    //     // 如果需要分页器
    //     pagination: {
    //       el: ".swiper-pagination",
    //       //分页器类型
    //       type: "bullets",
    //       //点击分页器，切换轮播
    //       clickable: true,
    //     },
    //     //自动轮播
    //     // autoplay: {
    //     //   delay: 1000,
    //     //   //新版本的写法：目前是5版本
    //     //   // pauseOnMouseEnter: true,
    //     //   //如果设置为true，当切换到最后一个slide时停止自动切换
    //     //   stopOnLastSlide: true,
