    //     navigation: {
    //       nextEl: ".swiper-button-next",
    //       prevEl: ".swiper-button-prev",
    //     },
    //     //切换效果
    //     // effect: "cube",
    //   });
    // }, 1000);
  },
  computed: {
    ...mapState({
      bannerList: (state) => state.home.bannerList,
    }),
  },
  watch: {
    //bannerList刚开始是一个空数组，向服务器发送请求并返回后变成一个非空数组
    bannerList: {
      handler(newVal, oldVal) {
        //$nextTick是vm上的方法，所以vc也可以使用
        //可以确保页面上有结构的时候再执行回调，经常与其他插件一起使用
        this.$nextTick(() => {
          let mySwiper = new Swiper(this.$refs.mySwiper, {
            //设置轮播图防线
            direction: "horizontal",
            //开启循环模式
            loop: true,
            // 如果需要分页器
            pagination: {
              el: ".swiper-pagination",
              //分页器类型
              type: "bullets",
              //点击分页器，切换轮播
              clickable: true,
            },
            //自动轮播
            // autoplay: {
            //   delay: 1000,
            //   //新版本的写法：目前是5版本
            //   // pauseOnMouseEnter: true,
            //   //如果设置为true，当切换到最后一个slide时停止自动切换
            //   stopOnLastSlide: true,
            //   //用户操作swiper之后，是否禁止autoplay
            //   disableOnInteraction: false,
            // },
            // 如果需要前进后退按钮
            navigation: {
              nextEl: ".swiper-button-next",
              prevEl: ".swiper-button-prev",
            },
            //切换效果
            // effect: "cube",
          });
        });
      },
    },
  },
};
</script>

